import axios from '@/libs/api.request'


export const getLogList = ({ beginTime,
                             endTime,
                             searchName,
                             searchOp,pageSize, pageNumber}) => {
  const data = {
    beginTime,
    endTime,
    userName:searchName,
    searchOp,
    pageSize,
    pageNumber
  }
  return axios.request({
    url: '/log/listLog',
    params:data,
    method: 'get'
  })
}





