<template>
  <div class="log-info">
    <div class="content">
      <div class="header-sub">
        <div class="title">
          <div class="breadcrumb-item-link">平台日志</div>
        </div>
      </div>
      <div class="filter-search">
        <div class="filter-search-left">
          <div class="filter-input mr-12">
            <Input class="filter-input"
                   type="text"
                   :maxlength="50"
                   v-model="searchName"
                   @on-enter="loadTableData(true,true)"
                   clearable
                   placeholder="请输入用户/姓名" />
          </div>
          <div class="filter-input mr-12">
            <DatePicker type="daterange"
                        v-model="timeRange"
                        format="yyyy/MM/dd"
                        placement="bottom-end"
                        @on-change="loadTableData(true,true)"
                        placeholder="请选择结束时间"
                        ></DatePicker>
          </div>
          <div class="filter-btn">
            <Button type="default"
                    @click="loadTableData(true)"
                    :loading="isQuerying">
              <span class="iconfont icon-chazhao"></span>
            </Button>
          </div>
        </div>

      </div>
      <div class="user-table no-scroll">
        <Table class="m-table no-border no-scroll"
               :loading="isQuerying"
               :row-class-name="(row,index)=>index%2===1?'even':''"
               stripe
               :columns="tableColumns"
               :data="tableData"></Table>
      </div>
      <div class="pagination">
        <Page style=""
              @on-change="onPaginationChange"
              @on-page-size-change="onPaginationSizeChange"
              :current="pageInfo.pageIndex"
              :page-size="pageInfo.pageSize"
              :total="pageInfo.totalElements"
              show-sizer
              show-total />
      </div>
    </div>
  </div>
</template>

<script>
import { getLogList } from '@/api/log'
import moment from 'moment'

export default {
  name: 'log-info',
  i18n_prefix: 'page.log-info.log-info',
  data () {
    return {
      searchName: '',
      timeRange: [
        moment().add(-6, 'd').format('YYYY/MM/DD'),
        moment().format('YYYY/MM/DD')
      ],
      searchOp: '',
      opList: [],

      tableColumns: [
        {
          title: '用户名',
          key: 'userName',
          align: 'left'
        },
        {
          title: '姓名',
          key: 'fullName',
          align: 'left'
        },
        {
          title: '时间',
          key: 'opTime',
          align: 'left'
        },
        {
          title: '操作类型',
          key: 'opType',
          align: 'left'
        },
        {
          title: '操作内容',
          key: 'detail',
          align: 'left'
        },
        {
          title: 'IP',
          key: 'clientIp',
          align: 'left'
        },
        {
          title: '所属区域',
          key: 'orgName',
          align: 'left'
        }
      ],
      tableData: [],
      pageInfo: {
        pageIndex: 1,
        pageSize: 10,
        totalElements: 0
      },

      isQuerying: false
    }
  },
  methods: {
    loadTableData (clear, hideTip) {
      console.log(this.timeRange)
      if (!this.timeRange[0]) {
        if (!hideTip) this.$Message.info('请选择时间范围')
        return
      }

      if (clear) {
        this.pageInfo.pageIndex = 1
      }
      this.isQuerying = true
      let beginTime =
        this.timeRange[0] &&
        moment(moment(this.timeRange[0])).format('YYYY-MM-DD 00:00:00')
      let endTime =
        this.timeRange[1] &&
        moment(moment(this.timeRange[1])).format('YYYY-MM-DD 23:59:59')

      getLogList({
        beginTime,
        endTime,
        searchName: this.searchName,
        searchOp: (this.searchOp || []).join(','),
        pageNumber: this.pageInfo.pageIndex,
        pageSize: this.pageInfo.pageSize
      })
        .then((res) => {
          this.tableData = res.data.result || []
          this.pageInfo.totalElements = res.data.totalElements || 0
        })
        .finally(() => {
          this.isQuerying = false
          console.log('--------', this.timeRange)
        })
    },
    onPaginationChange (index) {
      this.pageInfo.pageIndex = index
      this.loadTableData(false)
    },
    onPaginationSizeChange (size) {
      this.pageInfo.pageSize = size
      this.loadTableData(true)
    }
  },
  mounted () {
    this.loadTableData(true)
  }
}
</script>

<style lang="less" scoped>
@import '../../style/varient';
/deep/.ivu-date-picker input{
  width: 240px !important;
}
@size-padding: 10px;
.log-info {
  width: 100%;
  height: 100%;

  .content {
    width: 100%;
    height: 100%;
    box-shadow: 0px -1px 2px 0px rgba(0, 0, 0, 0.05),
      /*上边阴影  红色*/ -1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*左边阴影  绿色*/ 1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*右边阴影  蓝色*/ 0px 1px 2px 0px rgba(0, 0, 0, 0.05); /*下边阴影  黄色*/
    padding: 15px;
  }
}
</style>



